@import '../../global.scss';

.port {
    background-color: rgb(122, 106, 109);
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 60px;
    }

    ul {
        margin: 80px;
        padding: 0;
        list-style: none;
        display: flex;
    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item {
            width: 400px;
            height: 200px;
            border-radius: 20px;
            border: 1px solid rgb(36, 24, 146);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 0.5s ease;
            cursor: pointer;

            a {
                position: absolute;
                font-size: 30px;
                color: maroon;
                text-decoration: none;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
                
            .linkto {
                z-index: 3;
            }

            }

            &:hover {
                background-color: $mainColor;

                img {
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}