@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    

  }

  .left {
    flex: 0.5;
    overflow: hidden;
    background-color: maroon;
    opacity: 0.8;
    flex-direction: column;

    @include mobile {
        margin: -100px;
    }

    .imgcontainer {
      margin: 25%;
      margin-top: 25%;
      width: 400px;
      height: 400px;
      // background color of the image
      background-color: darkslategray;
      border-radius: 80%;
      display: flex;
      flex-direction: column;


      img {
        height: 98.9%;

        @include mobile {
            align-items: center;
            height: 100%;
        }
      }
    }
  }

  .right {
    flex: 0.6;
    background-color: maroon;
    position: relative;
    opacity: 0.8;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h1 {
        font-style: 30px;
        font-size: 80px;
        margin: 10px 0;
      }

      h2 {
        font-style: 50px;
        font-size: 50px;
      }

      h3 {
        font-style: 30px;
        margin: 5px 0;

        span {
          font-size: inherit;
          color: orangered;
        }

        .ityped-cursor {
          animation: blink 1s infinite;
        }

        @keyframes blink {
          50% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      }
    }

    a {
      position: absolute;
      bottom: 10px;
      color: white;
      left: 0%;
      animation: arrowBlink 2s infinite;

      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
