.work {
    background-color: rgb(146, 180, 175);
    display: table;
    align-items: center;

    .top {
        flex: 0.33;
        display: table-row;
        height: 150px;

        h1 {
            text-align: center;
            font-size: 60px;
            margin-left: 50px;
        }
    }

    .middle {
        flex: 0.33;
        display: table-row;

        h2 {
            color: rgb(22, 79, 145);
            margin: 20px;
            margin-left: 50px;
            text-align: center;
        }

        h4 {
            margin: 10px;
            margin-left: 125px;
        }
    }

    .bottom {
        flex: 0.33;
        display: table-row;
        text-align: center;
        color: rgb(22, 79, 145);
        
        h3 {
            margin: 40px;
        }
    }
}