.app {
    height: 100vh;

    .sections {
        width: 100%;
        height: calc(100vh - 70px);
        background-color: lightseagreen;
        position: relative;
        top: 70px;

        //goes through all pages when clicking href to the top of page
        scroll-behavior: smooth;

        // snap scrolling 
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //firefox
        &::-webkit-scrollbar{ //every other browser
            display: none;
        }

        // for each element in sections
        > * {
            width: 100vw;
            height: calc(100vh - 70px);
            // scrolls at the beginning of the section 
            scroll-snap-align: start;

        }
    }
}