.contacts {
    background-color: rgb(226, 193, 166);
    display: flex;

    .left{
        flex: 1;
        overflow: hidden;

        img {
            height: 100%;
        }
    }

    .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        //justify-content: space-around;
        justify-content: space-evenly;


        h2 {
            font-size: 60px;
            color: maroon;
        }

        form {
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;


            input {
                width: 650px;
                height: 70px;
                font-size: 40px;
                color: maroon;
                background-color: lightsalmon;
            }

            textarea {
                    width: 650px;
                    height: 300px;
                    font-size: 20px;
                    color: maroon;
                    background-color: lightsalmon;
            }

            button {
                width: 200px;
                height: 40px;
                color: coral;
                background-color: maroon;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                font-size: 25px;
                cursor: pointer;

                &.focus {
                    outline: none;
                }
            }

            span{
                color: green;
                
            }
        }
    }
}

